/*reset*/
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body:not(.keyboardNavigation) *:focus {
    outline: none;
}

button,
input {
    background: none;
    border: none;
    font-size: inherit;
    font-family: inherit;
    border-radius: 0;
    height: 2.5rem;
    height: var(--input-height)
}

svg {
    stroke: black;
    fill: black;
    width: 100%;
    height: 100%;
}

a {
    color: slategray;
}

/*common styles*/
:root {
    --input-height: 2.5rem;
    --padding: 0.5rem;
    --desktop-min-width: 900px;
}

.visuallyHidden {
    border: none;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

.btn {
    border: solid 1px lightgray;
    display: inline-block;
    padding: 0.5rem;
    padding: var(--padding);

}

.iconButton {
    width: 2.5rem;
    width: var(--input-height);
    height: 2.5rem;
    height: var(--input-height);
    padding: 0.5rem;
    padding: var(--padding);
}

.iconButton svg {
    width: 100%;
    height: auto;
}

/*page layout*/
html,
body {
    height: 100%;
}

body {
    font-family: 'Lato', sans-serif;
}

/*Animations*/
store-list {
    will-change: transform;
}

@-webkit-keyframes fade-in {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fade-in {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@-webkit-keyframes fade-out {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

@keyframes fade-out {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

@-webkit-keyframes slide-in-left {
    0% {
        -webkit-transform: translateX(-105%);
                transform: translateX(-105%);
    }

    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@keyframes slide-in-left {
    0% {
        -webkit-transform: translateX(-105%);
                transform: translateX(-105%);
    }

    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@-webkit-keyframes slide-out-right {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);        
    }

    100% {
        -webkit-transform: translateX(-105%);
                transform: translateX(-105%);
    }
}

@keyframes slide-out-right {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);        
    }

    100% {
        -webkit-transform: translateX(-105%);
                transform: translateX(-105%);
    }
}

.invisible {
    opacity: 0;
}

.visible {
    opacity: 1;
}

.fade-in {
    -webkit-animation: fade-in 0.3s forwards;
            animation: fade-in 0.3s forwards;
}

.fade-out {
    -webkit-animation: fade-out 0.3s forwards;
            animation: fade-out 0.3s forwards;
}

.slide-in-left {
    -webkit-animation: slide-in-left 0.3s forwards;
            animation: slide-in-left 0.3s forwards;
}

.slide-out-right {
    -webkit-animation: slide-out-right 0.4s forwards;
            animation: slide-out-right 0.4s forwards;
}

.hidden {
    display: none !important;    
}

.hiddenLight {
    visibility: hidden;
}
.searchOpener {
  border: solid 1px lightgray;
  position: absolute;
  z-index: 10;
  background-color: white;
  margin: var(--padding);
}

@media screen and (min-width: 900px) {
  .searchOpener {
      display: none;
  }
}

.leftPanel {
  display: flex;
  flex-direction: column;
  margin: var(--padding);
  padding: var(--padding);
  position: absolute;
  z-index: 20;
  background-color: white;
  height: calc(100vh - (var(--padding) * 2));
  width: calc(100% - (var(--padding) * 2));
  box-shadow: 4px 4px 4px lightgray;
  will-change: transform;
  -webkit-animation: slide-out-right 0.3s forwards;
          animation: slide-out-right 0.3s forwards;
}

.leftPanel.open {
  -webkit-animation: slide-in-left 0.3s forwards;
          animation: slide-in-left 0.3s forwards;
}

.storesAndStoreDetails {
  position: relative;
  overflow: hidden;
  flex: 1 1;
}

@media screen and (min-width: 900px) {
  .leftPanel.hiddenLight,
  .leftPanel {
      visibility: visible;
      -webkit-animation: slide-in-left forwards;
              animation: slide-in-left forwards;
      width: 24rem;
  }
}


.searchBox {
  --border-thickness: 2px;
  margin-bottom: 1rem;
}
.searchInputAndButtons {
  display: flex;
}
.searchInputAndIcons {
  display: flex;
  border: solid var(--border-thickness) black;
  margin-right: 0.5rem;
  flex: 1 1;
}
.searchSuggestions {
  position: absolute;
  left: 0;
  width: 100%;
  bottom: 0;
  -webkit-transform: translateY(calc(100% + 1px));
          transform: translateY(calc(100% + 1px));
}
.searchSuggestions [open="true"] ul {
  border: solid 1px lightgray;
  box-shadow: 2px 2px 2px lightgray;
}
.searchSuggestions ul {
  background-color: white;
  padding: var(--padding);
}
.searchIcon {
  width: var(--input-height);
  height: var(--input-height);
  padding: var(--padding);
}
.searchInput {
  flex: 1 1;
  position: relative;
}
.geolocalizeIcon {
  height: calc(var(--input-height) + (var(--border-thickness) * 2));
  width: calc(var(--input-height) + (var(--border-thickness) * 2));
}

.resultsNumber {
  padding: var(--padding);
}

.storeDetails {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: white;
}
.backButton {
  background-color: black;
  color: white;
  display: flex;
  width: 100%;
  align-items: center;
  margin-bottom: 1rem;
}
.backIcon {
  width: var(--input-height);
  height: var(--input-height); 
  display: inline-block;
}
.backIcon svg {
  stroke: white;
  width: 100%;
  height: 100%;
}
.storeDetails .store {
  padding: var(--padding);
}
.storeDetails .store_name {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
  font-weight: bold;
}
.storeDetails .store_mail,
.storeDetails .store_phone {
  display: flex;
  align-items: center;
}
.storeDetails .store_mail {
  margin-bottom: 0.25rem;
}
.storeDetails .store_phone {
  margin-bottom: 0.75rem;
}
.storeDetails .icon {
  width: 1rem;
  height: 1rem;
  margin-right: 0.5rem;
  display: inline-block;
  vertical-align: middle;
}
.storeDetails .store_mail,
.storeDetails .store_categories {
  margin-bottom: 0.5rem;
}
.storeDetails .store_categories li {
  display: inline;
}
.storeDetails .store_categories li::after {
  content: ", ";
}
.storeDetails .store_categories li:last-child::after {
  content: ".";
}
.storeDetails .store_categoriesTitle,
.storeDetails .store_servicesTitle {
  font-size: 1.2rem;
  margin-bottom: 0.25rem;
}

.storesList ul {
  padding: var(--padding);
  height: 100%;
  overflow-y: scroll;
}

.storesList ul::-webkit-scrollbar {
  width: 0.3em;
}

.storesList ul::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

.storesList ul::-webkit-scrollbar-thumb {
  background-color: black;
  outline: 1px solid slategrey;
}
.storesList .storeName {
  margin-bottom: 0.5em;
}
.storesList .storePhone .icon {
  width: 1em;
  height: 1em;
  display: inline-block;
  vertical-align: middle;
}

.filterPanel {
  padding: var(--padding);
}
.filter-panel_toggleBtn {
  display: inline-flex;
  align-items: center;
}
.filter-panel_toggleBtn_text {
  margin-right: 0.5rem;
}
.filter-panel_arrow svg {
  width: var(--input-height);
  height: auto;
  transition: -webkit-transform 400ms;
  transition: transform 400ms;
  transition: transform 400ms, -webkit-transform 400ms;
}
.open .filter-panel_arrow svg {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.storeTypeBtn .icon,
.removeIcon {
  width: 1rem;
  height: 1rem;
  margin-right: 0.5rem;
  display: inline-block;
  vertical-align: middle;
}
.mapWrapper {
  height: 100vh;
}
